export function sortedCountriesList(language: string): iCountry[] {
    if (language === 'ar') {
        return countriesList.sort((a, b) => a.short_name_ar.localeCompare(b.short_name_ar));
    }
    return countriesList.sort((a, b) => a.short_name_en.localeCompare(b.short_name_en));
}

export function getCountryByKey(keyName: string, keyValue: string): iCountry {
    let target = null;

    for (let i = 0; i < countriesList.length; i++) {

        const country = countriesList[i];
        if (country[keyName as keyof typeof country].trim().toLowerCase() == keyValue.toLowerCase().trim()) {
            target = country;
            break;
        }
    }

    return target;
}

export function getCountryPropByKey(keyName: string, keyValue: string, prop: string) {
    let target = null;

    for (let i = 0; i < countriesList.length; i++) {

        const country = countriesList[i];
        if (country[keyName as keyof typeof country].trim().toLowerCase() == keyValue.toLowerCase().trim()) {
            target = country[prop as keyof typeof country];
            break;
        }
    }

    return target;
}

export default countriesList;